import React, {Component} from 'react';
import { render } from 'react-dom';
import Img from './Img';
import Paginacion from './Paginacion';


class Resultado extends Component{     
  
    mostrarImagenes = () =>{
        //Declarar variable para usarse en condicional   
        let imagenes=this.props.imgs;    
        //Condicional para no mostar al no haber imagenes 
        if(imagenes.length===0) return null;   

        return(
          <React.Fragment> 
            <div className="m-2 row" >          
            {imagenes.map(               
                imagen => (
                <Img  
                key={imagen.id} 
                vimg={imagen} 
                />
                ))}
                <div className="justify-content-center row col-lg-12"> 
                   <Paginacion
                       pa={this.props.pagAnt}
                       ps={this.props.pagSig}
                       pagina={this.props.pagActual}
                       ultimaPagina={this.props.ultpag}
                   />
                </div>
                             
            </div>   
          </React.Fragment> 
        )
        //console.log(Imagenes);
    }    

    render(){
        return( 
            <React.Fragment>
              {this.mostrarImagenes()}                              
            </React.Fragment>            
        );
    }
}

export default Resultado;