import React, { Component }   from "react";

import '../App.css';
import logo from '../assets/Logo.gif';

//Importar URL de servicio
import {Apiurl} from '../services/apirest';
//Importal axios comando terminal => npm install --save axios
/*-----Funcionar con Apache PHP---
<IfModule headers_module>
    #
    # Avoid passing HTTP_PROXY environment to CGI's on this or any proxied
    # backend servers which have lingering "httpoxy" defects.
    # 'Proxy' request header is undefined by the IETF, not listed by IANA
    #
    ##RequestHeader unset Proxy early
Header set Access-Control-Allow-Origin: *
Header set Access-Control-Allow-Headers: *
</IfModule>
 */
import axios from 'axios';


class Login extends Component{

   /* constructor(props){
      super(props);
    }*/
    // Estado con la misma estructura del formulario y el api que vamos a consumir
    state={
        form:{
            "usuario":'',
            "password":''
        },
        error:false,
        errorMsg:""
    }
    //Previene que se envie el formulario sin escribir nada
    manejadorSubmit = e => {
        e.preventDefault();
    }

    manejadorInputs = async e =>{
        await this.setState({
            form:{
                ...this.state.form,
                [e.target.name]:e.target.value
            }
        })
        console.log(this.state.form);
    }

    manejadorBotton = () => {
        //tomar variale de la url guardada en services y concatena url especifica
        let url=Apiurl+'auth';
        //Valida que los campos no esten vacios
        if((this.state.form.usuario).trim()!=='' || (this.state.form.password).trim()!==''){
        //Ejecuta el metodo de llamado de apirest por medio de post
        axios.post(url,this.state.form).then(response => {
            //Valida si la respuesta fue ok
           if(response.data.status==='ok'){
               //Guarda el token para validar del lado del servidor
               localStorage.setItem('Token',response.data.result.token);
               //redirecciona a la vista apirest
               this.props.history.push('../ApiRest');
           }else{
               //guarda stado de error para ser mostrado
               this.setState({
                   error:true,
                   errorMsg:response.data.result.error_msg
               })    
           }
           //si ocurre un error no controlado
        }).catch(error =>{
            console.log(error);
            this.setState({
                   error:true,
                   errorMsg:'Error al conectar con la API'
                }
            )
        })
    }else{  //si no se llenadoron los campos usuario y passs     
            this.setState({
                   error:true,
                   errorMsg:'Por favor llena todos los campos'
                }
            )
    }
    }

    render(){
         return (
            <React.Fragment>
                <br/><br/>
               <div className="wrapper fadeInDown">
                <div id="formContent">
                        <div className="faceIn first">
                            <br/><br/>
                        <img src={logo} id="icon" alt="logo" />
                        </div>
                        <form onSubmit={this.manejadorSubmit}>
                            <input type="text"  className="fadeIn second " name="usuario" placeholder="usuario" onChange={this.manejadorInputs}  />
                            <input type="password"  className="fadeIn third " name="password" placeholder="password" onChange={this.manejadorInputs} />
                            <br/>
                            <br/>
                            <input type="submit" className="fadeIn fourth" value="Entrar" onClick={this.manejadorBotton} />
                        </form>
                        
                        {//si la variable cumple condicion muestra
                        this.state.error===true &&
                        <div id="formFooter">                            
                            <div className="alert alert-danger" role="alert">
                              <strong>oops!</strong> {this.state.errorMsg}
                            </div>
                        </div>
                         }
                </div>
               </div>
            </React.Fragment>                     
         );
    }
} 

export default Login;