import React, { Component } from 'react';
// se importa component para poder usarse en el componente principal
import Buscador from './componentes/Buscador'; 
import Resultado from './componentes/Resultado'; 

//Importa Jquery
import $ from 'jquery';

class App extends Component{
 
state = {
  termino : '',
  imagenes:[],
  pagina: '',
  Tpags:''
}  


/*btn = async () =>{
  alert(this.state.pagina);
  if(this.state.pagina===1){
    //$('#btn-ant').hide();
  }else{
    //$('#btn-ant').show();
  }
} */


scroll = () =>{
  const elemento = document.querySelector('.jumbotron');
  elemento.scrollIntoView({block: "end", behavior: "smooth"});
}

PaginaAnterior = () => {
    //leer pagina actual
    let pag=this.state.pagina;
    //si pagina es igual a 1 no seguir restando
    if(pag===1){ return; }
    //restar pagina
    pag--;    

    //Actualizar state
    this.setState({
      pagina:pag
    }, () => {
      this.consultarApi();
      this.scroll();
    })
    //console.log('Anterior');
}

PaginaSiguiente = () => {
  //leer pagina actual
  let pag=this.state.pagina;
  //se debe verificar numero de paginas para parar en el max
  //code
  //Sumar pagina
  pag++;
  //Actualizar state
  this.setState({
    pagina:pag
  }, () => {
    this.consultarApi();
    this.scroll();
  }) 
  //console.log('Siguiente');
}

DataSearch = (termino) => {
  //se da valor a las propiedades de state
  this.setState({
    termino : termino,
    pagina : 1
  },// despues de agregar el termino ejecuta metodo consultarApi
  () => {
    this.consultarApi();
  })
}
  
render () {
  return (
    <div className="app container">
      <div className="jumbotron" >
        <p className="lead text-center">Bucador</p>          
        <Buscador 
            Data={this.DataSearch}
        />         
      </div>
        <Resultado
           imgs={this.state.imagenes}
           pagAnt={this.PaginaAnterior}
           pagSig={this.PaginaSiguiente}
           pagActual={this.state.pagina}
           ultpag={this.state.Tpags}
        /> 
                   
    </div>
  );
}

consultarApi = () => {
  const page=this.state.pagina;  
  const url = 'https://pixabay.com/api/?key=19222224-e1b0e63e157e5eb8974955c22&q='
  +this.state.termino
  +'&par_page='+24
  +'&page='+page;
  //Se usa fetch para abrir url y se formatea el resultado json
  //Se utiliza el metodo setState para modificar la propiedad imagenes con el resultado
  fetch(url)
  .then(respuesta => respuesta.json())
  .then(resultado => this.setState({imagenes:resultado.hits}))
  //.then(resul => console.log(resul.hits))
  //console.log(url);
}


}

export default App;
