import React, { Component } from 'react';
//import LazyLoad from 'react-lazyload';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import '../App.css';

const Img = (props) => { 

    const {webformatURL,largeImageURL,previewURL,tags,views,likes} = props.vimg;
    
    return(       
       <div className="col-12 col-sm-6 col-md-3 col-lg-3 mb-4">
         <div className="card img-hidden" >
             <img className="img-fluid card-img-top" src={previewURL} 
             alt={tags}/>
             <div className="card-body"> 
                <p className="card-text">{likes} Me gusta</p> 
                <p className="card-text">{views} Vistas</p> 
                <a href={largeImageURL} target="_blank" className="btn btn-block btn-primary" >
                  Ver Imagen
                </a>
             </div>
         </div>  
       </div>
    )       
}

export default Img;