//import { camelCase } from 'jquery'; Linea deprecated
//import $ from 'jquery';
import React, { Component } from 'react';



class Buscador extends Component {

    //Tomar valor del input (Con ref)
    busquedaRef = React.createRef();
    
    //Metodo tipo flecha se ejecuta al hacer submit al formulario
    handlerData = (e) => {
        //Metodo para evitar ejecutar en primera carga
        e.preventDefault();
        //Enviamos el valor de input al componente principal, por medio del props
        this.props.Data(this.busquedaRef.current.value);
        //console.log();      
    }

    render(){
        return (
            <form onSubmit={this.handlerData}>
                <div className="row" >
                    <div className="input-group mb-3 p-2">
                           <input ref={this.busquedaRef} type="text" className="form-control col-lg-10" placeholder="Buscar Imagen..." />   
                           <div className="input-group-append">
                               <button type="submit" className="btn btn-outline-primary"> 
                                    Buscar
                               </button>   
                            </div>                           
                    </div>
                </div>
            </form>
        )
    }
}

export default Buscador;