import React from 'react';

const Paginacion = props => {
   
    return (
        <div className="py-3"> 
         <button id="btn-ant" onClick={props.pa} type="button" className="btn btn-info mr-2" > &larr; Anterios </button>
         Pagina: {props.pagina} de  {props.ultimaPagina}
         <button id="btn-sig" onClick={props.ps} type="button" className="btn btn-info " > Siguiente &rarr; </button>        
        </div>
    )
}
export default Paginacion;